<template>
  <div class="order-list">
    <div v-title>{{ title }}</div>
    <service-select :showDropDown="serviceDropDown">
    </service-select>
    <transition name="fade">
      <div
        v-show="serviceDropDown"
        class="overlay"
        @click="serviceDropDown = false">
      </div>
    </transition>
    <div class="select">
      <span :class="{'service-name': serviceDropDown}">所有服务站点</span>
      <span :class="[{'icon-up': serviceDropDown}, {'icon-down': !serviceDropDown}]" @click="serviceDropDown = !serviceDropDown"></span>
    </div>
    <mt-navbar v-model="selected" :value="selected">
      <mt-tab-item id="pageAllOrder">
        <span>全部</span>
        <mt-badge v-if="selected === 'pageAllOrder'">30</mt-badge>
      </mt-tab-item>
      <mt-tab-item id="pageWaitingReceived">
        <span>待收货</span>
        <mt-badge v-if="selected === 'pageWaitingReceived'">30</mt-badge>
      </mt-tab-item>
      <mt-tab-item id="pageWaitingServed">
        <span>待送达</span>
        <mt-badge v-if="selected === 'pageWaitingServed'">30</mt-badge>
      </mt-tab-item>
      <mt-tab-item id="pageServed">
        <span>已送达</span>
        <mt-badge v-if="selected === 'pageServed'">30</mt-badge>
      </mt-tab-item>
      <mt-tab-item id="pageCancel">
        <span>已取消</span>
        <mt-badge v-if="selected === 'pageCancel'">30</mt-badge>
      </mt-tab-item>
    </mt-navbar>
    <!--tab-container-->
    <mt-tab-container v-model="selected" ref="wrapper" class="page-loadmore-wrapper">
      <!-- 全部 -->
      <mt-tab-container-item :id="selected">
        <!-- bug：自动上拉加载数据，设置 初始化时会自动检测它的高度是否能够撑满其容器 = 否
          :autoFill="false"
        -->
        <mt-loadmore
          v-if="orderList.length"
          :autoFill="false"
          :top-method="loadTop"
          :bottom-method="loadBottom"
          @bottom-status-change="handleBottomChange"
          :bottom-all-loaded="allLoaded"
          ref="loadmore">

          <div class="order-info" v-for="(item, index) in orderList" :key="index">
            <div class="com-list line">
              <p>
                <span class="text-gray">订单编号：00098777899988</span>
                <span class="text-right text-red" v-if="true">待收货</span>
                <span class="text-right text-green" v-else-if="false">已送达</span>
                <span class="text-right text-gray" v-else-if="false">已取消</span>
              </p>
            </div>
            <div class="com-list">
              <p>
                <span class="">卢瑶瑶</span>
                <span class="text-right">13388499000</span>
              </p>
            </div>
            <ul>
              <li>
                <img src="../../assects/images/key-yes.png" alt="">
                <div class="img-right">
                  <h3>
                    <span>物品</span>
                    <!-- <span v-else>{{ item.productName }}</span> -->
                    <span class="text-right">
                      <span class="c-orange">555</span>  噢啦豆
                    </span>
                  </h3>
                  <p>
                    <!-- <span class="sit-info padding-top5" v-if="item.attrUnitName">
                      {{ item.attrValue }}{{ item.attrUnitName }}
                    </span> -->
                    <span v-if="!item.attrUnitName" class="sit-info padding-top5">
                      <!-- <span v-if="item.orderDetailTags" class="sit-info">
                        <span v-for="(list, index) in item.orderDetailTags">
                          {{ list.name }}
                          <i v-if="item.orderDetailTags.length-1 !== index">，</i>
                        </span>
                      </span> -->

                      <span>{{ item.productName }}</span>
                    </span>
                    <span v-else>{{ item.attrValue }}{{ item.attrUnitName }}</span>
                  </p>
                </div>
              </li>
            </ul>
            <div class="com-list detail-btn">
              <span @click="goOrderDetail(item.orderNo)">查看详情</span>
            </div>
            <div class="bgc-gray"></div>
          </div>
        </mt-loadmore>
      </mt-tab-container-item>
    </mt-tab-container>
  </div>
</template>

<script>
  import { Navbar, TabItem, TabContainer, TabContainerItem, Loadmore, Toast, Badge } from 'mint-ui'
  import serviceSelect from './components/service-select'
  export default {
    data () {
      return {
        title: '订单列表',
        serviceDropDown: false,
        orderList: [1, 2],
        allLoaded: false,
        pageNum: 1, // 页数, 默认: 1
        pageSize: 10, // 一页显示记录数, 默认:10
        selected: this.$route.query.selectedId
      }
    },
    components: {
      [Navbar.name]: Navbar,
      [TabItem.name]: TabItem,
      [TabContainer.name]: TabContainer,
      [TabContainerItem.name]: TabContainerItem,
      [Loadmore.name]: Loadmore,
      [Badge.name]: Badge,
      serviceSelect
    },
    created () {
      this.token = localStorage.getItem('token')
    },
    watch: {
      selected: function (val, oldVal) {
        // 请求接口刷新数据，要重新刷新列表数据，同时重置分页参数
        this.serviceList = ''
        this.pageNum = 1
        this.getOrderList()
      }
    },
    methods: {
      getServiceList () {
        // 切换tab，恢复，数据已全部获取完毕标识
        this.allLoaded = false
//        OrderList({
//          pageNum: this.pageNum,
//          pageSize: this.pageSize,
//          m: this.selected
//        }).then(res => {
//          if (res.status === 'true') {
//            this.serviceList = res.info.result
//            this.serviceListTotal = res.info.total
//            Toast('已刷新')
//            this.$refs.loadmore.onTopLoaded()
//            下拉仅展示第一页的数据
//            this.pageNum = 1
//          } else {
//            // 请求失败的时候，请求数据的接口，显示网络错误
//            Toast(res.msg)
//          }
//        })
      },
      goOrderDetail (productId) {
        this.$router.push({path: 'order-details', query: { orderNo: productId }})
      },
      loadTop () {},
      loadBottom () {},
      handleBottomChange () {}
    }
  }
</script>

<!-- 设置mint-ui组件的样式，style 标签去掉scoped -->
<style lang="less">
  .order-list .mint-tab-item-label {
    font-size: 16px;
  }
</style>
<style lang="less" scoped>
  .fade-enter-active, .fade-leave-active {
    transition: opacity .5s
  }
  .fade-enter, .fade-leave-to /* .fade-leave-active below version 2.1.8 */ {
    opacity: 0
  }
  div.overlay {
    position: fixed;
    top: 0;
    left: 0;
    width: 100vw;
    height: 100vh;
    z-index: 1;
    background: rgba(0, 0, 0, .5);
  }
  .order-list {
    .select {
      display: flex;
      align-items: center;
      justify-content: center;
      height: 51.5px;
      width: 100vw;
      font-size: 16px;
      color: #191D21;
      line-height: 16px;
      border-bottom: 1px solid #E9ECF5;
      position: fixed;
      top: 0;
      left: 0;
      background: white;
      z-index: 3;
      span {
        margin-right: 10px;
        &.service-name {
          color: #FFB717;
        }
        &.icon-up {
          width: 0;
          height: 0;
          border-left: 6px solid transparent;
          border-right: 6px solid transparent;
          border-bottom: 9px solid #FFB717;
        }
        &.icon-down {
          width: 0;
          height: 0;
          border-left: 6px solid transparent;
          border-right: 6px solid transparent;
          border-top: 9px solid #CDCED3;
        }
      }
    }
    .mint-navbar {
      padding-top: 51.5px;
      .mint-tab-item {
        font-size: 16px;
        padding: 20px 0 14.5px;
        border-bottom: 2px solid #E5E5E5;
        position: relative;
        .mint-badge {
          position: absolute;
          top: 5px;
          right: 0;
          font-size: 11px;
          line-height: 10px;
          background: #FF4848;
          border-radius: 100px;
        }
      }
      .mint-tab-item.is-selected {
        color: #ffb717;
        border-color: #ffb717;
        margin-bottom: 0px;
      }
    }
    .mint-tab-item-label {
      font-size: 16px;
      color: #ffb717;
    }
    .mint-tab-container {
      .order-info {
        margin-bottom: 10px;
        padding: 7px 0;
        background-color: #fff;
        font-size: 14px;
        line-height: 23px;
        .bgc-gray {
          height: 10px;
          background-color: #F0F2F9;
        }
        .com-list, .order {
          p {
            .text-right {
              float: right;
              font-size: 13px;
            }
            .text-red {
              color: #FF4848;
            }
            .text-green {
              color: #1EBB00;
            }
            .text-gray {
              color: #9D9DA5;
            }
            .text-orange {
              color: #FFB717;
            }
            .text-black {
              font-weight: 600;
            }
          }
        }
        .com-list {
          padding: 14px 15px;
          &.line {
            border-bottom: 1px solid #E9ECF5;
          }
          &.detail-btn {
            display: flex;
            justify-content: flex-end;
            span {
              color: #9D9DA5;
              border: 1px solid #CDCED3;
              border-radius: 4px;
              line-height: 25px;
              padding: 0 16px;
            }
          }
        }
        .order {
          padding: 10px 15px;
          .text-right {
            border: 1px solid #FFB717;
            border-radius: 4px;
            line-height: 25px;
            padding: 0 16px;
          }
        }
        ul {
          overflow: hidden;
          background-color: #F0F2F9;
        }
        li {
          display: flex;
          justify-content: space-between;
          height: 80px;
          padding: 10px;
          box-sizing: border-box;
          overflow: hidden;

          img {
            width: 60px;
            height: 60px;
          }
          .a-left {
            display: block;
            float: left;
          }
          .img-right {
            position: relative;
            width: 100%;
            float: left;

            h3 {
              display: -webkit-box;
              -webkit-line-clamp: 1;
              -webkit-box-orient: vertical;
              font-size: 14px;
              line-height: 1;
              padding-top: 9px;
              padding-left: 10px;
            }
            .text-right {
              position: absolute;
              top: 24px;
              right: 0;
              font-size: 12px;
            }
            p {
              clear: both;
              display: block;
              color: #b6bfc8;
              padding-top: 5px;
              padding-left: 10px;
              word-break: break-all;
              width: 73%;
              display: -webkit-box;
              -webkit-line-clamp: 2;
              -webkit-box-orient: vertical;
              overflow: hidden;

              .sit-info {
                // float: left;
                display: -webkit-box;
                -webkit-line-clamp: 2;
                -webkit-box-orient: vertical;
                overflow: hidden;
              }
              .padding-top5 {
                padding-top: 5px;
              }
            }
            .assess-situation {
              float: left;
            }
          }
        }
      }
    }
  }
</style>



