<template>
  <transition name="center">
    <div class="service-select" v-if="showDropDown">
      <div class="service-list">
        <ul class="content">
          <li v-for="(item, index) in serviceList" :key="index">
            <div>服务点名称</div>
          </li>
        </ul>
      </div>
      <div class="btn">
        <span>清除筛选</span>
        <span class="confirm">确定</span>
      </div>
    </div>
  </transition>
</template>

<script>
  export default {
    name: 'service-select',
    data () {
      return {
        serviceList: [1, 2, 3, 1, 2, 3, 1, 2, 3, 2, 3, 1, 2, 3, 1, 2, 3, 1, 2, 3, 1]
      }
    },
    props: {
      showDropDown: {
        type: Boolean
      }
    }
  }
</script>

<style lang="less" scoped>
  .center-enter,
  .center-leave-to {
    transform: translateY(-200%);
  }

  .center-enter-to,
  .center-leave {
    transform: translateY(0);
  }

  .center-enter-active,
  .center-leave-active {
    transition: transform .28s ease-in-out;
  }
  li {
    list-style: none;
    font-size: 13px;
  }
  .service-select {
    padding-top: 50.5px;
    position: fixed;
    top: 0;
    left: 0;
    z-index: 2;
    background: white;
    .service-list {
      width: 100vw;
      height: 276px;
      overflow: scroll;
      ul.content {
        margin: 15px;
        overflow: hidden;
        li {
          background: white;
          float: left;
          width: 33.3333%;
          padding: 0 7px 0 0 ;
          box-sizing: border-box;
          margin-bottom: 10px;
          div {
            height: 35px;
            background: white;
            display: flex;
            align-items: center;
            justify-content: center;
            border-radius: 3px;
            border: 1px solid #CDCED3;
          }
        }
      }
    }
    .btn {
      width: 100vw;
      height: 45px;
      font-size: 16px;
      line-height: 16px;
      border-top: 1px solid #CDCED3;
      border-bottom: 1px solid #CDCED3;
      display: flex;
      align-items: center;
      justify-content: center;
      span {
        display: flex;
        align-items: center;
        justify-content: center;
        width: 50vw;
        height: 45px;
        font-size: 16px;
        color: #66666F;
        line-height: 16px;
        &.confirm {
          background: linear-gradient(135deg, #ffB717 0%, #FF8E00 100%);
          color: #ffffff;
        }
      }
    }
  }
</style>



